import Link from "next/link";
import { BarChart3, LayoutDashboard, Palette, Settings2, Sparkles, UsersRound } from "lucide-react";
import { ThemeToggle } from "@/components/theme/theme-toggle";

const mainLinks = [
  { href: "/dashboard", label: "Dashboard", icon: LayoutDashboard },
  { href: "/patients", label: "Pacientes", icon: UsersRound },
  { href: "/templates", label: "Templates", icon: Sparkles },
  { href: "/analytics", label: "Análises", icon: BarChart3 },
];

const settingsLinks = [
  { href: "/settings/profile", label: "Perfil", icon: Settings2 },
  { href: "/settings/branding", label: "Identidade visual", icon: Palette },
  { href: "/settings/subscription", label: "Assinatura", icon: BarChart3 },
];

export function ProfessionalShell({ children }: { children: React.ReactNode }) {
  return (
    <div className="min-h-screen bg-background px-2 py-2 md:px-4 md:py-4">
      <div className="mx-auto grid min-h-screen w-full max-w-7xl md:grid-cols-[260px_1fr]">
        <aside className="kv-surface hidden rounded-3xl px-4 py-6 md:block">
          <Link href="/dashboard" className="mb-6 inline-flex items-center gap-2 text-lg font-semibold tracking-tight">
            <Sparkles className="size-4 text-primary" />
            KoreVitta
          </Link>

          <p className="mb-2 px-2 text-[11px] font-medium uppercase tracking-wider text-muted-foreground">
            Profissional
          </p>
          <nav className="space-y-1">
            {mainLinks.map((link) => {
              const Icon = link.icon;
              return (
                <Link
                  key={link.href}
                  href={link.href}
                  className="flex items-center gap-2 rounded-xl px-3 py-2.5 text-sm text-muted-foreground hover:bg-accent/75 hover:text-foreground"
                >
                  <Icon className="size-4" />
                  {link.label}
                </Link>
              );
            })}
          </nav>

          <p className="mb-2 mt-6 px-2 text-[11px] font-medium uppercase tracking-wider text-muted-foreground">
            Configurações
          </p>
          <nav className="space-y-1">
            {settingsLinks.map((link) => {
              const Icon = link.icon;
              return (
                <Link
                  key={link.href}
                  href={link.href}
                  className="flex items-center gap-2 rounded-xl px-3 py-2.5 text-sm text-muted-foreground hover:bg-accent/75 hover:text-foreground"
                >
                  <Icon className="size-4" />
                  {link.label}
                </Link>
              );
            })}
          </nav>
        </aside>

        <div className="flex min-h-screen flex-col md:pl-4">
          <header className="kv-surface rounded-3xl px-4 py-3 md:px-6">
            <div className="flex items-center justify-between">
              <Link href="/dashboard" className="font-semibold md:hidden">
                KoreVitta • Pro
              </Link>
              <nav className="flex gap-2 overflow-x-auto text-sm md:hidden">
                {mainLinks.map((link) => {
                  const Icon = link.icon;
                  return (
                    <Link
                      key={link.href}
                      href={link.href}
                      className="inline-flex whitespace-nowrap rounded-full border border-border/80 bg-card/60 px-3 py-1"
                    >
                      <span className="mr-1.5 inline-flex items-center"><Icon className="size-3.5" /></span>
                      {link.label}
                    </Link>
                  );
                })}
              </nav>
              <div className="flex items-center gap-2">
                <ThemeToggle />
                <Link
                  href="/patients/new"
                  className="kv-glow hidden rounded-xl bg-linear-to-r from-primary to-chart-1 px-3 py-2 text-sm font-medium text-primary-foreground md:inline-flex"
                >
                  + Novo Paciente
                </Link>
              </div>
            </div>
          </header>

          <main className="flex-1 px-2 py-6 md:px-3 md:py-7">{children}</main>
        </div>
      </div>
    </div>
  );
}
